.nav {
    grid-area: n;
    background-color: rgb(255, 255, 255);
    padding: 20px;
    
}

.item a{
    color: rgb(0, 0, 0);
    text-decoration: none;
    font-size: 90%;
    border: #fbfbff;
}
.item a:hover{
    font-family: Brush Script MT, Brush Script Std, cursive;

    font-weight: bold;
    transition: all 0.2s ease;
}

.item a.activeLink {
    color: white;
    background: lavender;
    border:3px solid lavender;
    border-radius: 5px;
    font-weight: bold;
    transition: all 0.6s ease;
}