.all
{
    background-color: rgb(255, 255, 255);
}
.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1220px;
    grid-template-areas: "h h" "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;   
    background-color: rgb(255, 255, 255);
    

}

.app-wrapper-content {
    grid-area: c;
    background-color: rgb(255, 255, 255);


}











